
/**
 * @description 
 * @param {String} msg 
 * @param {Object} data 
 */
function msg(msg = "",data = null){
    return {msg,data}
}
let map = new Map(), storage = uni.getStorageInfoSync(); //
console.log('cache.js');
/**
 * @description 
 * @class class
 */
class Cache {
    /**
     * @constructor
     * @param {Number} timeout 864000
     */
    constructor(timeout = 86400) {
        // map
        storage.keys.forEach(key=>map.set(key,uni.getStorageSync(key)));
        this.map = map;//map
        this.timeout = timeout; 
    }
    /**
		 * @description 
		 * @property {String} key key
		 * @property {Object} data data
		 * @property {String} timeout 0
		 */
    set(key, data, timeout = this.timeout) {
        //data = value
        Object.assign(data,{createTime:Date.now(),timeout});
        uni.setStorageSync(key,data);//
        this.map.set(key,data);//map
        return msg("",data);
    }
		/**
		 * @description 
		 * @property {String} key key
		 */
    get(key){
        let value = this.map.get(key); //
        if(!value)return msg("key"); //
        //                             ()
        let {timeout,createTime,...data} = value,
				presentTime = Date.now(),
				tdoa = (presentTime -createTime) / 1000;
        // 
        if(timeout != 0 && tdoa > timeout){
            uni.removeStorageSync(key)
            this.map.delete(key) //mapkey
            return msg("");
        }else{
            return msg("ok",data);
        }
    }
		/**
		 * @description 
		 * @property {String} key key
		 */
    remove(key){
        uni.removeStorageSync(key)//
        this.map.delete(key) //mapkey
        return msg("");
    }
		/**
		 * @description 
		 */
    clear(){
        uni.clearStorageSync();//
        this.map.clear() //map
        return msg("");
    }
		/**
		 * @description 
		 */
		getSize(cb){
			plus.cache.calculate(size => {
				let sizeCache = parseInt(size);
				let cacheSize;
				if (sizeCache == 0) {
					cacheSize = '0B';
				} else if (sizeCache < 1024) {
					cacheSize = sizeCache + 'B';
				} else if (sizeCache < 1048576) {
					cacheSize = (sizeCache / 1024).toFixed(2) + 'KB';
				} else if (sizeCache < 1073741824) {
					cacheSize = (sizeCache / 1048576).toFixed(2) + 'MB';
				} else {
					cacheSize = (sizeCache / 1073741824).toFixed(2) + 'GB';
				}
				cb(cacheSize)
			});
		}
}
export default new Cache();
